.home-page
{
    opacity: 0;
    position: relative;
    z-index: -1000;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    top: 0;
    left: 0;
    margin: 0 auto;
    background-color: #fff;

    transition: opacity .6s;
}

