    -webkit-transform: rotate(0);
            transform: rotate(0);
    cursor: auto;
}
.project-drawer__techStack
{
    list-style-type: none;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.project-drawer__techStack--tech
{
    text-transform: lowercase;

    padding: .5rem 1rem;
    margin: .5rem 1rem;
    margin-left: 0;

    background-color: var(--theme-blue);

    font-family: var(--font-sans-serif);
    font-size: .9rem;
    font-weight: 300;
    letter-spacing: 1px;
    color: #fff;

    box-shadow: 0 0 3px 3px rgba(40, 40, 40, .4);
}
.portfolio-page
{
    opacity: 0;
    width: 80%;
    max-width: 72rem;
    min-height: 100vh;
    margin: 0 auto;

    display: -webkit-flex;

    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;

    -webkit-transition: opacity .6s;

    transition: opacity .6s;
}

@media screen and (max-width: 1099px)
{
    .portfolio-page
    {
        min-height: 0;
        margin: 6rem auto;
        width: 70%;
    }
}
*
{
    padding: 0;
    margin: 0;
}

/* DEFINE VARIABLES */
:root
{
    --theme-blue: #3a738f;
    --facebook-blue: #3b5998;
    --twitter-blue: #00aced;
    --instagram-violet: #bc2a8d;
    --linkedin-blue: #0077b5;

    --font-sans-serif: 'Raleway', sans-serif;
    --font-serif: 'Roboto Slab', serif;
}

.fade-in
{
    opacity: 1 !important;
}
